/** Media Queries **/
.no-proyectos a {
  color: #4338CA;
}

.listado-proyectos {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .listado-proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .listado-proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.listado-proyectos .proyecto {
  background: linear-gradient(to right, #0891B2 0%, #0ab9e3 100%);
  min-height: 12rem;
  display: flex;
  align-items: center;
  padding: 2rem;
  line-height: 2;
  border-radius: 0.5rem;
  font-size: 2rem;
  font-weight: 600;
  transition: all 0.3s ease;
}
.listado-proyectos .proyecto:hover {
  background-color: #077d9a;
  transform: scale(1.05);
}
.listado-proyectos .proyecto a {
  color: #FFFFFF;
}